import React, { useEffect, useState, Component } from "react";
import axios from "axios";
import { Link } from "react-router-dom";

import "bulma/css/bulma.min.css";
import "./style.scss";

function Car({ match }) {
  const [car, setCar] = useState({});
  const id = match.params.id;

  useEffect(() => {
    axios({
      method: "GET",
      url: `http://carbikerental.std-953.ist.mospolytech.ru/api/cars/${id}/`,
    }).then((response) => {
      setCar(response.data);
    });
  }, [id]);

  return (
    
    <div>
      <Link to="/cars">Назад</Link>
      <h1>Авто</h1>
      <div className="card">
        <div className="card-image">
          <figure className="image is-4by3">
            <img src={car.photo} alt="car image" />
          </figure>
        </div>
        <div className="card-content">
          <div className="media">
            <div className="media-content">
              <p className="title is-4">{car.brand}</p>
              <p className="subtitle is-6">{car.price}</p>
            </div>
          </div>

          <div className="content">
            {car.year} года выпуска. <br />
            {car.mileage} км пробега. <br />
          </div>
        </div>
      </div>
    </div>
  )
}

export default Car;
